import React, { useState } from 'react';
import Quiz from '../component/quiz';
import {
  StartButton,
  MainWrap,
  MainWrapBox,
  StartWrap,
} from '../style/StyleQuiz';

function Main() {
  const [isTest, setIsTest] = useState(false);

  const handleTestStart = (): void => {
    setIsTest(true);
  };

  return (
    <MainWrapBox>
      <MainWrap>
        {isTest ? (
          <Quiz />
        ) : (
          <StartWrap>
            <h1>2022     ?</h1>
            <p>
                !   ... <br />
                      ! <br />
                     ?
            </p>
            <div>
              <StartButton onClick={handleTestStart}></StartButton>
            </div>
            {/* <img /> */}
          </StartWrap>
        )}
      </MainWrap>
    </MainWrapBox>
  );
}

export default Main;
